@import "../../css-config/mixins.scss";

.cart_panel {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  z-index: 99;
  .layer {
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .cart_content {
    width: 100%;
    height: 100%;
    background: white;
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    transform: translateX(100%);
    animation: slideCart 0.6s ease forwards;
    @include md {
      width: 340px;
    }
    .cart_header {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 20px 15px;
      border: 1px solid rgba(0, 0, 0, 0.3);
      .header {
        flex-grow: 1;
        margin-bottom: 0;
        font-size: 20px;
        font-weight: 700;
        text-transform: uppercase;
      }
      .close_btn {
        display: flex;
        align-items: center;
        gap: 5px;
        cursor: pointer;
        color: #c41134;
        svg {
          font-size: 18px;
        }
        .text {
          text-transform: uppercase;
          font-size: 14px;
        }
        &:hover {
          opacity: 0.5;
        }
      }
    }
    .empty-cart {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 20px;
      margin-top: 100px;
      svg {
        font-size: 120px;
        opacity: 0.2;
      }
      .return_btn {
        outline: 0;
        border: 0;
        height: 40px;
        width: 150px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        font-size: 16px;
        color: white;
        background: #c41134;
        border-bottom: 3px solid #c41134;
      }
    }

    .cart_footer {
      border-top: 1px solide rgba(0, 0, 0, 0.5);
      .subtotal {
        padding: 20px 15px;
        border-bottom: 1px solide rgba(0, 0, 0, 0.5);
        display: flex;
        justify-content: space-between;
        .text {
          margin-bottom: 0;
          font-size: 20px;
          font-weight: 700;
          text-transform: uppercase;
          &.total {
            color: #c41134;
          }
        }
      }
      .button {
        padding: 20px 15px;
        .checkout_btn {
          outline: 0;
          border: 0;
          height: 50px;
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          font-size: 13px;
          color: white;
          background: #c41134;
          border-bottom: 3px solid #c41134;
        }
      }
    }
  }
}

@keyframes slideCart {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0%);
  }
}
