@import "../../css-config/mixins.scss";
.category_main_content {
  padding: 30px 0;
  background-color: #2a2a2a;
  @include md {
    padding: 75px 0;
  }
  .layout {
    max-width: calc(100% - 20px);
    margin: 0 auto;
    @include md {
      max-width: 1200px;
    }
    .category_title {
      font-size: 24px;
      color: white;
      @include md {
        font-size: 34px;
      }
    }
    .products_container {
      margin: 20px 0;
      @include md {
        margin: 50px 0;
      }
    }
  }
}
